import { commonParams, options } from './config'
import axios from 'axios'
import jsonp from 'common/js/jsonp'
import { getUid } from 'common/js/uid'
import { ERR_OK } from 'api/config'

export function getLyric(mid) {
  const url = `${process.env.REACT_APP_BASE_URL}/api/lyric`
  const data = Object.assign({}, commonParams, {
    songmid: mid,
    pcachetime: +new Date(),
    g_tk: 67232076,
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0,
    format: 'json'
  })

  return axios
    .get(url, {
      params: data
    })
    .then(res => {
      return Promise.resolve(res.data)
    })
}

export function getVKey(songmid, filename) {
  const url = 'https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg'

  const data = Object.assign({}, commonParams, {
    cid: 205361747,
    format: 'json',
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0,
    uin: 0,
    songmid,
    filename,
    guid: getUid()
  })

  return jsonp(
    url,
    data,
    Object.assign({}, options, {
      param: 'callback'
    })
  )
}

export function getSongsUrl(songs) {
  // const url = debug
  //   ? '/api/getPurlUrl'
  //   : 'http://ustbhuangyi.com/music/api/getPurlUrl'

  const url = `${process.env.REACT_APP_BASE_URL}/api/getPurlUrl`
  let mids = []
  let types = []

  songs.forEach(song => {
    mids.push(song.mid)
    types.push(0)
  })

  const urlMid = genUrlMid(mids, types)

  const data = Object.assign({}, commonParams, {
    g_tk: 5381,
    format: 'json',
    platform: 'h5',
    needNewCode: 1,
    uin: 0
  })
  return new Promise((resolve, reject) => {
    let tryTime = 3

    function request() {
      return axios
        .post(url, {
          comm: data,
          req_0: urlMid
        })
        .then(response => {
          const res = response.data
          if (res.code === ERR_OK) {
            let urlMid = res.req_0
            if (urlMid && urlMid.code === ERR_OK) {
              const purlMap = {}
              urlMid.data.midurlinfo.forEach(item => {
                if (item.purl) {
                  purlMap[item.songmid] = item.purl
                }
              })
              if (Object.keys(purlMap).length > 0) {
                resolve(purlMap)
              } else {
                retry()
              }
            } else {
              retry()
            }
          } else {
            retry()
          }
        })
    }

    function retry() {
      if (--tryTime >= 0) {
        request()
      } else {
        reject(new Error('Can not get the songs url'))
      }
    }

    request()
  })
}

function genUrlMid(mids, types) {
  const guid = getUid()
  return {
    module: 'vkey.GetVkeyServer',
    method: 'CgiGetVkey',
    param: {
      guid,
      songmid: mids,
      songtype: types,
      uin: '0',
      loginflag: 0,
      platform: '23'
    }
  }
}
